.pageWarp{
    min-width: 100vh;
    min-height: 100vh;
    display: flex;
    flex-direction: column;

}
.content{

    width: 100%;
    flex: 1;
}
.ant-spin-container{
    min-height: 563px;
}
#logo{
    color: white;
    margin-bottom: 0;
    text-align: center;
    justify-content: center;
}
.ant-layout{
    flex: 0;
}
.wrap{
    max-width: 980px;
    margin: 0 auto;
    position: relative;
    /*border: 1px solid gainsboro;*/
}
.divider{
    height: 44px;
    margin-left: 14px;
}
#nav{
  display: inline-block;
    background: transparent;
    margin-top: -2px;
}
#nav a{
    color: white;
}
#nav a:hover{
    color: dodgerblue;
}
#nav .ant-menu-item-selected a{
    color: dodgerblue;
}
.xsNav{
    position:absolute;
    right: -230px;
    top:0;
    width: auto;
    text-align: right;
}
.ant-list-item-meta{
    align-items: center;
}
img{
    width: 100%;
}
